<template>
  <div>
      <form ref="form" @submit.prevent="sendEmail">
          <h4 v-bind:class="{'contactTextLight': color, 'contactTextDark': !color}">YOUR NAME</h4>
          <div>
              <input v-bind:class="{'inputLight': color, 'inputDark': !color}" type="text" 
              v-model="name" name="user_name">
          </div>
          
          <h4 v-bind:class="{'contactTextLight': color, 'contactTextDark': !color}">YOUR EMAIL</h4>
          <input v-bind:class="{'inputLight': color, 'inputDark': !color}" type="text" name="user_email"
            v-model="email">
          <h4 v-bind:class="{'contactTextLight': color, 'contactTextDark': !color}">YOUR MESSAGE (YOU CAN ADD HERE YOUR PHONE NUMBER IF NECESSARY)</h4>
          <textarea v-bind:class="{'inputLight': color, 'inputDark': !color}" name="message" id="" 
           v-model="message" cols="20" rows="6"></textarea>

          <div class="d-flex justify-end">
              <button v-bind:class="{'btnLight': color, 'btnDark': !color}" value="Send"> SEND</button>
          </div>

      </form>

      <v-alert class="mt-3" :value="alert"
        color="#32CD30"
        dark
        border="top"
        icon=" mdi-checkbox-marked-circle"
        >
        Thanks for contacting me. I'll write you back within 24 hrs.
      </v-alert>
       <v-alert class="mt-3" :value="alertFail"
        dismissible
        color="red"
        dark
        border="top"
        icon=" mdi-cancel"
        transition="scale-transition">
        Something went wrong. Please try again
      </v-alert>
      <v-alert class="mt-3" v-if="showErrorMessage"
        color="red"
        dark
        border="top"
        icon="mdi-cancel"
        transition="scale-transition">
        {{errorMessage}}
      </v-alert>

  </div>
</template>

<script>
import emailjs from '@emailjs/browser';

export default {
  components: {  },
    name:"ContactForm",
    props:{
        color:Boolean
    },
    data() {
        return {
            alert: false,
            alertFail: false,
            name:"",
            email:"",
            message:"",
            errorMessage:"",
            showErrorMessage:false
        };
    },
    methods: {
        sendEmail() {
            switch(this.checkData()){
                case true:
                    emailjs.sendForm('service_80n0yvi', 'template_bax1yh9', this.$refs.form, 'BWMISw0kuP1EGkdLp')
                        .then((result) => {
                            console.log('SUCCESS!', result.text);
                            this.alert = true;
                            this.alertFail = false;
                            window.setInterval(() => {
                                this.eraseSuccessAlert(true)
                            }, 6000)
                        }, (error) => {
                            console.log('FAILED...', error.text);
                            this.alertFail = true;
                            this.alert = false;
                            window.setInterval(() => {
                                this.eraseSuccessAlert(false)
                            }, 6000)
                    });
                    break;
                case 'name':
                    this.showErrorMessage = true;
                    this.errorMessage = "Write your name please."
                    break;
                case 'email':
                    console.log("Se mostrara el email")
                      this.showErrorMessage = true;
                    this.errorMessage = "Write your email please."
                    break;
                case 'msg':
                    console.log("Se mostrara el msg")
                    this.showErrorMessage = true;
                    this.errorMessage = "Leave a message please"
                    break;
            }
             
        },
        checkData(){
            console.log("Va sendEmail");
            this.showErrorMessage = false;
            this.alert = false;
            this.alertFail = false;
            if(this.name.length < 1){
                return "name"
            } else if(this.email.length< 1){
                console.log("Paso el nombre checara el email"+ this.showErrorMessage)
                return "email"
            } else if(this.message.length < 1) {
                return "msg"
            } else{
                return true
            }
        },
        eraseSuccessAlert(success){
            if(success){
                this.alert  = false;
            } else{
                this.alertFail = false;
            }
            
        }
    },
    created(){
        this.eraseSuccessAlert();
    }
}
</script>

<style>
    .contactTextLight{
        color: white;
    }
    .contactTextDark{
        color: black;
    }
    .inputLight{
        border: 2px solid #32CD30;
        background-color: transparent;
        width: 100%;
        color: white;
    }
    .inputDark{
        border: 2px solid #1A4314;
        background-color: #D9D9D9;
        width: 100%;
        color: #1A4314;
    }

    .btnLight{
        border-radius: 4px;
        border: 2px solid #32CD30;
        background-color: #32CD30;
        width: 203px;
        height: 44px;
        color: white;
        font-size:20px;
        line-height: 1.5;
    }
    .btnDark{
        border-radius: 4px;
        border: 2px solid #1A4314;
        background-color: #D9D9D9;
        width: 203px;
        height: 44px;
        color: #1A4314;
        font-size: 25px;
        line-height: 1.5;
    }

    

</style>